
a {
  text-decoration: none !important;
  color: #555;
}
p {
  font-family: "Helvetica Neue", "STHeiti";
}
@media screen and (min-width: 1000px) {
#app:before {
    content: '';
    width: 100%;
    height: 15px;
    position: absolute;
    background-color: #f75341;
}
}
main {
  width: 100%;
}
main :after {
  content: '';
  display: block;
  clear: both;
}
.container {
  width: 100%;
  max-width: 1000px;
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
}
@media screen and (max-width: 1000px) {
.container {
    transform: translateY(-80px);
    padding: 5%;
}
}
.container .item {
  width: 500px;
  height: 500px;
  float: left;
}


